.about-container {
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background-color: white;
}

.about-content {
  max-width: 800px;
  padding: 2.5rem;
  background-color: white;
  border-radius: 10px;
}

.about-content h1 {
  color: #333;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
  text-align: center;
}

.about-content p {
  color: #666;
  line-height: 1.8;
  font-size: 1.1rem;
  text-align: justify;
}

/* Responsive design */
@media (max-width: 768px) {
  .about-container {
    padding: 0.5rem;
  }
  
  .about-content {
    padding: 1.0rem;
  }
  
  .about-content h1 {
    font-size: 2rem;
  }
  
  .about-content p {
    font-size: 1rem;
  }
}
