.header {
  background-color: #F4F1EA;
  border-bottom: 1px solid #D8D8D8;
  padding: 0.5rem 0;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  text-decoration: none;
}

.logo h1 {
  color: #382110;
  font-size: 24px;
  font-weight: bold;
  margin: 0;
}

.codes-text {
  color: #755e45;
  font-weight: normal;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.nav-link {
  color: #382110;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  padding: 0.5rem;
}

.nav-link:hover {
  color: #755e45;
}

.search-button {
  background: none;
  border: none;
  color: #382110;
  cursor: pointer;
  font-size: 16px;
  padding: 0.5rem;
}

.search-button:hover {
  color: #755e45;
}

.search-container {
  flex: 1;
  max-width: 500px;
}

.search-input {
  width: 100%;
  padding: 0.5rem 1rem;
  border: 1px solid #dfe1e5;
  border-radius: 24px;
  font-size: 0.875rem;
  outline: none;
  transition: box-shadow 0.2s;
}

.search-input:focus {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-color: transparent;
}

.spinner {
  /* Add your spinner styles */
}

.error-message {
  color: #d93025;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}